import React from 'react';
import logo from './logo.svg';
import './App.css';
import { Switch, Route, Link } from 'react-router-dom';

import HomePage from './pages/homepage/homepage.component.jsx'
import ShopPage from './pages/shop/shop.component.jsx';
import Header from './components/header/header.component.jsx';
import SignInandSignUpPage from './pages/sign-in-and-sign-up-page/sign-in-and-sign-up-component.jsx';

import { auth, createUserProfileDocument } from './firebase/firebase.utils';

const Hats = () => {
  return <h1>HATS PAGE</h1>;
}

const TopicList = (props) => {
  return (
    <div>
      <h1>
        <Link to={`${props.match.url}/15`}>15</Link>
        <Link to={`${props.match.url}/20`}>20</Link>
        <Link to={`${props.match.url}/25`}>25</Link>
        <button onClick={() => props.history.push(`${props.match.url}/40`)}>40</button>
      </h1>
    </div>
  )
}

const TopicDetails = (props) => {
  return (
    <div>
      <h1>
        Topic List {props.match.params.topicId}
      </h1>
    </div>
  )
}

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      currentUser: null
    }
  }

  unSubscribeFromAuth = null;
  componentDidMount() {
    this.unSubscribeFromAuth = auth.onAuthStateChanged(async (userAuth) => {
      if (userAuth) {
        const userRef = await createUserProfileDocument(userAuth);
        userRef.onSnapshot((snapshot) => {
          this.setState({
            currentUser: {
              id: snapshot.id,
              ...snapshot.data()
            }
          }, () => console.info(this.state.currentUser));
        })
      }
      this.setState({ currentUser: userAuth });
      console.info(userAuth);
    })
  }
  componentWillUnmount() {
    this.unSubscribeFromAuth();
  }
  render() {
    return (<div>
      <Header currentUser={this.state.currentUser} />
      <Route exact path="/" component={HomePage} />
      <Route exact path="/shop" component={ShopPage} />
      <Route exact path="/signin" component={SignInandSignUpPage} />
      <Route exact path="/topic" component={TopicList} />
      <Route exact path="/topic/:topicId" component={TopicDetails} />
    </div>
    )
  }
}

export default App;
